.Product{
    padding:10px;
    
    background-color: rgba(228, 156, 156, 0.623);
    
    margin:5%;
   
}
.productContainer{
    margin-left:15%;
    margin-right:15%;
    margin-bottom:10px;
    display:grid;
    grid-template-columns: 33% 33% 33%;
}
.POST{
    background-color:rgba(102, 102, 102, 0.445);
    padding:2%;
    margin:5%;
    color:rgb(241, 241, 241);
}
.Product button{
    margin:2%;
    outline: none;
    border-style: none;
    width:70px;
    height:60px;
    background-color: black;
    color:white;
    border-radius:10px;
}